.slider {
    position:relative;
    width:100%;
}
.slider .pagination {
    display:none;
}
@media (min-width: $(desktop)) {
    .slider .pagination {
        display:block;
    }
}
.slider .pagination a {
    display:block;
}
.slides {
    width:100%;
    position:relative;


}
.slides .slide {
    left:100%;
    position:absolute;
    width:100%;
    -webkit-box-align: center;
        -ms-flex-align: center;
                -ms-grid-row-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    opacity:0;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}
.slides .slide img {
    width:100%;
    height:100%;
    -o-object-fit:none;
       object-fit:none;
    -o-object-position:bottom;
       object-position:bottom;
}
.slides .slide:nth-child(1){
    left:0;
    opacity:1;
    position:relative;
}
@media (min-width: $(desktop)) {
    .slides .slide:nth-child(1) {
        position:absolute;
    }
}
